import { Component, OnInit } from '@angular/core';
import {FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-enroll',
  templateUrl: './enroll.component.html',
  styleUrls: ['./enroll.component.css']
})
export class EnrollComponent implements OnInit {
  formModel: FormGroup; 
  constructor(private fb: FormBuilder) {
    this.formModel = fb.group({
      email: [null, [Validators.required, this.emailValidator]],
      passwords: fb.group({
        password: [null, [Validators.required, Validators.minLength(6)]],
        passwordConfirm: [null, [Validators.required, Validators.minLength(6)]]
      }, { validator: this.equalValidator})
    }); 
  }

  ngOnInit() {

  }
  onSubmit() {
    if ( this.formModel.valid) {
      console.log(this.formModel.value);
      console.log('OK');
    } else {
      console.log(this.formModel.value);
      console.log('error');
    }
  }

  /*  校验邮编 和 校验电话*/
  emailValidator(control: FormControl): any {
    if (!control.value)
      return null;
    var emailReq = /\w@\w*\.\w/;
    // var mobileReq = /^(13[0-9]{9})|(15[89][0-9]{8})$/;
    let emaiValid = emailReq.test(control.value);
    // let mobileValid = mobileReq.test(control.value);
    // if (emaiValid)
    console.log('邮箱校验' + emaiValid);
    return emaiValid ? null : { email: { errorInfor: '邮箱格式错误'}};
    // else (mobileValid)
    //   return mobileValid ? null : { mobile: { error: '电话号码错误' } };
  } 
  /*  校验前后两次输入密码正确  */
  equalValidator( group: FormGroup): any {
    const password = group.get('password').value;
    const pConfirm = group.get('passwordConfirm').value;
    let validEqual: boolean = (password === pConfirm);
    console.log('前后两次密码比较' + validEqual);
    return validEqual ? null : { equal: {errorInfor: '前后两次输入密码不同'}};

  }
}



